 .side-bar .split-pane-visible >.split-pane-side {
    min-width: 88px; /* Default: 270px */
    max-width: 88px; /* Same as when menu opens in mobile mode */
    transition: all 1s ease-in-out;
  }
  .side-bar   .split-pane-visible >.split-pane-side:hover {
    transition: all 1s ease-in-out;
    min-width: 300px; /* Default: 270px */
    max-width: 300px; /* Same as when menu opens in mobile mode */
  }



