 ion-progress-bar{
    --buffer-background: #333;
   height:8px!important;
}

 ::-webkit-scrollbar {
  width: 0px!important;  /* Remove scrollbar space */
  background: transparent!important;  /* Optional: just make scrollbar invisible */
}
ion-modal{
  --width: 90%!important;
  --height: 90%!important;
  --border-radius: 0;
}


ion-card{
    animation: fadein 1s;
    -moz-animation: fadein 1s;
    /* Firefox */
    -webkit-animation: fadein 1s;
    /* Safari and Chrome */
    -o-animation: fadein 1s;
    /* Opera */
}
ion-item{
  animation: growin 0.3s;
  -moz-animation: growin 0.3s;
  /* Firefox */
  -webkit-animation: growin 0.3s;
  /* Safari and Chrome */
  -o-animation: growin 0.3s;
  /* Opera */
  transition: all 0.3s;
}
ion-list{
  transition: all 0.3s;
}




@keyframes slidein {
  from {
    transform:translate3d(-1,-1,-1);
  }
  to {
    transform:translate3d(0,0,0);
  }
}
@-moz-keyframes slidein {
  /* Firefox */
  from {
    transform:scaleX(0);
    transform:scaleY(0);
  }
  to {
    transform:scaleX(1);
    transform:scaleY(1);
  }
}
@-webkit-keyframes slidein {
  /* Safari and Chrome */
  from {
    transform:scaleX(0);
    transform:scaleY(0);
  }
  to {
    transform:scaleX(1);
    transform:scaleY(1);
  }
}
@-o-keyframes slidein {
  /* Opera */
  from {
    transform:scaleX(0);
    transform:scaleY(0);
  }
  to {
    transform:scaleX(1);
    transform:scaleY(1);
  }
}

@keyframes growin {
  from {
    transform:scaleX(0);
    transform:scaleY(0);
  }
  to {
    transform:scaleX(1);
    transform:scaleY(1);
  }
}
@-moz-keyframes growin {
  /* Firefox */
  from {
    transform:scaleX(0);
    transform:scaleY(0);
  }
  to {
    transform:scaleX(1);
    transform:scaleY(1);
  }
}
@-webkit-keyframes growin {
  /* Safari and Chrome */
  from {
    transform:scaleX(0);
    transform:scaleY(0);
  }
  to {
    transform:scaleX(1);
    transform:scaleY(1);
  }
}
@-o-keyframes growin {
  /* Opera */
  from {
    transform:scaleX(0);
    transform:scaleY(0);
  }
  to {
    transform:scaleX(1);
    transform:scaleY(1);
  }
}



@keyframes fadein {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-moz-keyframes fadein {
    /* Firefox */
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-webkit-keyframes fadein {
    /* Safari and Chrome */
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-o-keyframes fadein {
    /* Opera */
    from {
        opacity:0;
    }
    to {
        opacity: 1;
    }
}



ion-menu ion-content {
  --padding-top: 20px;
  --padding-bottom: 20px;
  --background: var(--ion-item-background, var(--ion-background-color, #fff));
}
ion-content{
  overflow: hidden;
}
/* Remove background transitions for switching themes */
ion-menu ion-item {
  --transition: none;
}

ion-item.selected {
  --color: var(--ion-color-primary);
}

/*
 * RMF Custom Menu Style
*/
ion-menu.md ion-list {
  padding: 10px 0;
}

ion-menu.md ion-list-header {
  padding-left: 9px;
  padding-right: 9px;

  text-transform: uppercase;
  letter-spacing: .1em;
  font-weight: 450;
}

ion-menu.md ion-item {
  --padding-start: 18px;

  margin-right: 10px;

  border-radius: 0 50px 50px 0;

  font-weight: 500;
}

ion-menu.md ion-item.selected {
  --background: rgba(var(--ion-color-primary-rgb), 0.14);
}

ion-menu.md ion-item.selected ion-icon {
  color: var(--ion-color-primary);
}

ion-menu.md ion-list-header,
ion-menu.md ion-item ion-icon {
  color: var(--ion-color-step-650, #5f6368);
}

ion-menu.md ion-list:not(:last-of-type) {
  border-bottom: 1px solid var(--ion-color-step-150, #d7d8da);
}


/*
 * iOS Menu
*/
ion-menu.ios ion-list-header {
  padding-left: 16px;
  padding-right: 16px;

  margin-bottom: 8px;
}

ion-menu.ios ion-list {
  padding: 20px 0 0;
}

ion-menu.ios ion-item {
  --padding-start: 16px;
  --min-height: 50px;
}

ion-menu.ios ion-item ion-icon {
  font-size: 24px;
  color: #73849a;
}

ion-menu.ios ion-item.selected ion-icon {
  color: var(--ion-color-primary);
}



